<template>
  <div id="start">

    <!---->
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item :to="{ name:'start' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <div class="warp_filter">
      <template>
        <el-select v-model="filter.versions" placeholder="" @change="versions">
          <el-option
            v-for="(item,index) in versions.app_versions"
            :label="item.name"
            :value="item.name" :key="index">
          </el-option>
        </el-select>
      </template>
      <template>
        <el-select v-model="filter.channels" placeholder="" @change="versions">
          <el-option
            v-for="(item,index) in versions.app_channels"
            :label="item.name"
            :value="item.name" :key="index"
          >
          </el-option>
        </el-select>
      </template>

      <!---->
      <el-row :gutter="20" class="">
        <el-col :span="12">
          <span style="margin: 20px 0;display: inline-block"><i class="el-icon-menu" style="margin-right: 10px"></i></span>
          <div id="main" style="width: 99%;height: 500px;border: 1px solid #D3DCE6">

          </div>
        </el-col>
        <el-col :span="12">
          <span style="margin: 20px 0;display: inline-block"><i class="el-icon-menu"style="margin-right: 10px"></i></span>
          <table class="t1">
            <tbody class="table">
            <tr>
              <th></th>
              <th></th>
              <th></th>
            </tr>
            <tr v-for="(item,index) in tableData">
              <td>{{item.is_enabled}}</td>
              <td><span style="cursor: pointer;background-color: #333;padding: 1px 3px;color: #fff"
                        @click="details(item.is_enabled)">{{item.total}}</span></td>
              <td >{{item.percent}}</td>
            </tr>
            </tbody>
          </table>
        </el-col>
      </el-row>

      <!---->
      <el-dialog v-model="dialogTableVisible">
        <h2 class="dh2">:<span v-text="pageInfo.device_model"></span></h2>
        <el-table :data="dialogData" style="text-align: center">
          <el-table-column property="username" label="" width="150"></el-table-column>
          <el-table-column property="admin_username" label=""></el-table-column>
          <el-table-column label="" property="created_at" width="200"></el-table-column>
        </el-table>
        <div slot="footer">
          <el-pagination
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-size="10"
            layout="prev,next"
            class="page">
          </el-pagination>
        </div>
      </el-dialog>

    </div>

  </div>
</template>

<script>
  import * as API from '../../../api/api'
  import { mapGetters,mapActions } from 'vuex'
  export default {
      data(){
          return {
            /**/
            filter:{
              versions:'',
              channels:'',
            },
            /**/
            options:{
              title: {
                text:null
              },
              plotOptions: {
                pie: {
                  allowPointSelect: true,
                  cursor: 'pointer',
                  dataLabels: {
                    enabled: true,
                  }
                }
              },
              series: [{
                type: 'pie',
                name: '',
                data: [
                  ['a',10],
                  ['b',20],
                  ['c',30],
                  ['d',40]
                ]
              }],
              credits:false
            },
            /**/
            tableData:'',

            /**/
            dialogTableVisible:false,
            dialogData:'',
            pageInfo:{
              is:'',
              page:'',
              device_model:''
            },

            /**/
            currentPage:1,

          }
      },
      computed:{
        ...mapGetters(['versions']),
      },
      methods:{

        //
        Timestamp(row){

            if(row){
              const now = new Date(row*1000);
              var year=now.getFullYear();
              var month=now.getMonth()+1;
              var date=now.getDate();
              var hour=now.getHours();
              var minute=now.getMinutes();
              var second=now.getSeconds();

              return year+"-"+month+"-"+date+" "+hour+":"+minute+":"+second;
            }else{
                return false
            }

        },
        //
        line(json){
          var arr = [];
          for(var i = 0;i<json.length;i++){
            if(json[i]['is_enabled'] == true){
              json[i]['is_enabled'] = ''
            }else{
              json[i]['is_enabled'] = ''
            }
            arr.push([json[i]['is_enabled'],parseFloat(json[i].percent)])
          }
          return arr
        },

          /**/
          versions(){

          },

          /**/
          //
          getInfo(){


            var p = new Promise( (resolve,reject) => {
              const token = JSON.parse(window.sessionStorage.getItem('loginInfo')).token;
              this.$http({
                method:'GET',
                url:API.status_stat,
                headers: {'Authorization': token},
              }).then(function(res){

                if(res.status == 200){
                  resolve(res)
                }else{
                  reject(res)
                }

              })
            })
            return p;
          },
          //
          filterStar(data){
              return data == true ? '':'';
          },

          /**/
          /**/
          details(is){
              this.pageInfo.is = is;
              var options = {
                  page:1
              }
              this.getDetails(options);
          },
          //
          getDetails(parm){
            this.dialogTableVisible = true;
            let is = this.pageInfo.is;

            if(is){
                is = 1
            }else{
                is = 0
            }

            const token = JSON.parse(window.sessionStorage.getItem('loginInfo')).token;
            this.$http({
              method:'GET',
              url:API.status_stat_details,
              headers: {'Authorization': token},
              params:{
                is_enabled:is,
                limit:10,
                page:parm.page
              },
            }).then(res => {
              if(res.status == 200){
                this.dialogData = res.data.data.users;
              }
            })
        },

          /**/
          handleCurrentChange(val){
            this.currentPage = val;
            this.getDetails({page:val})
          }

      },
      mounted(){
          //
        this.getInfo().then( res => {
          this.tableData = res.data.data.items;
          this.options.series[0].data = this.line(res.data.data.items);
          this.$HighCharts.chart('main',this.options);
        })
      },
  }
</script>

<style scoped>
  #start{
    padding: 10px;
  }
  /**/
  #userInfo .breadcrumb{
    height: 30px;
    line-height: 30px;
  }
  .warp_filter{
    text-align: left;
    padding: 10px;
    background-color:#fff;
    border:1px solid #D3DCE6;
    margin-top: 20px;
  }
</style>

<style>
  .el-table__header-wrapper th>.cell{
    text-align: center;
  }
</style>



