<template>
  <div id="userInfo" v-loading.body="loading">

    <!---->
    <el-breadcrumb separator="/" class="breadcrumb">
      <el-breadcrumb-item :to="{ name:'userInfo' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!---->
    <div class="count">
      <div style="padding: 10px;">
        <h2><span></span>10</h2>
        <ul>
          <li>
            <div>
              <p>{{USERINFO_base.data.users_count}}</p>
              <h4></h4>
            </div>
          </li>
          <li>
            <div>
              <p>{{USERINFO_base.data.online_users_count}}</p>
              <h4></h4>
            </div>
          </li>
          <li>
            <div>
              <p>{{USERINFO_base.data.active_users_count}}</p>
              <h4></h4>
            </div>
          </li>
        </ul>
      </div>
    </div>

    <!---->
    <div class="warp_list">
      <!---->
        <div class="filter">
          <el-row>
            <div class="f">
              <span class="title"></span>
              <el-select v-model="filter.version" placeholder="">
                <el-option v-for="(item,index) in versions.app_versions" :label="item.name" :value="item.name" :key="index">
                </el-option>
              </el-select>
            </div>
            <div class="f">
              <span class="title"></span>
              <el-select v-model="filter.channel" placeholder="">
                <el-option v-for="(item,index) in versions.app_channels" :label="item.name" :value="item.name" :key="index">
                </el-option>
              </el-select>
            </div>
            <div class="f" style="margin-right: 15px">
              <span class="title"></span>
              <el-select v-model="filter.str" placeholder="">
                <el-option v-for="(item,index) in options1" :label="item.label" :value="item.value" :key="index">
                </el-option>
              </el-select>
            </div>
            <div class="f">
              <span class="title"></span>
              <el-select v-model="filter.time" placeholder="">
                <el-option v-for="(item,index) in options2" :label="item.label" :value="item.value" :key="index">
                </el-option>
              </el-select>
            </div>
            <div style="display: inline-block">
              <el-date-picker
                v-model="filter.start"
                type="date"
                placeholder="">
              </el-date-picker>
              <el-date-picker
                v-model="filter.end"
                type="date"
                placeholder="">
              </el-date-picker>
            </div>
            <el-button style="margin-left: 40px" @click="filtration"></el-button>
          </el-row>
          <!---->
          <el-row>
            <el-input v-model="ser" placeholder="" style="width:200px"></el-input>
            <el-button icon="search" @click
              ="search"></el-button>
          </el-row>
        </div>
        <!---->
      <table class="table">
        <thead>
        <tr>
          <th>ID</th>
          <th></th>
          <th></th>
          <th>UUID</th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th v-for="(item,index) in tableData[0].user_node_types" class="for1">
            {{item.name}}
            </th>
          <th v-for="(item,index) in tableData[0].user_node_types" class="for1">
            {{item.name}}
            </th>
          <th v-for="(item,index) in tableData[0].user_node_types" class="for1">
            {{item.name}}
            </th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th style="fixed:right"></th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,index) in tableData">
          <td>{{item.id}}</td>
          <td>{{item.username}}</td>
          <td>****</td>
          <td>{{item.uuid}}</td>
          <td>
            <el-tag
              :type="item.is_enabled ? 'success' : 'primary'"
              close-transition>{{item.is_enabled?'':''}}</el-tag>
          </td>
          <td>{{item.total_payment_amount}}</td>
          <td>{{item.current_coins}}</td>
          <td>{{item.total_used_coins}}</td>
          <td>{{item.group_name}}</td>
          <!---->
          <td v-for="(item2,index2) in item.user_node_types">
            {{item2.status}}
            </td>
          <!---->
          <td v-for="(item2,index2) in item.user_node_types">
            {{item2.used_count}}
            </td>
          <!---->
          <td v-for="(item2,index2) in item.user_node_types">
            {{item2.expired_at | Time}}
            </td>
          <td>{{item.last_app_launched_at | Time}}</td>
          <td>{{item.last_app_launched_version}}</td>
          <td>{{item.created_at | Time}}</td>
          <td>{{item.created_version}}</td>
          <td><el-button  size="small" @click="detail(item.id)"></el-button></td>
        </tr>
        </tbody>
      </table>
    </div>

    <!---->
    <el-pagination
      @current-change="handleCurrentChange"
      :current-page="USERINFO_base.data.current_page"
      layout="total, prev, pager, next, jumper"
      :page-size="pageSize"
      :total="USERINFO_base.data.users_count"
      class="page">
    </el-pagination>

    <!---->
    <user-detail :visab="dialogVisible" @closeDialog="cdialog"></user-detail>

  </div>
</template>

<script>
  import { mapGetters,mapActions } from 'vuex'
  import userDetail from '../publicView/accoutInfo/index.vue'
  export default{
    components:{
      userDetail
    },
    data(){
        return {
            data:[],
          //
          filter:{
            version:'',
            channel:'',
            str:'',
            time:'',
            start:'',
            end:''
          },
          ser:'',
          //app_versions:versions,
          options1: [
            {
              value: '',
              label: ''
            },{
            value: 1,
            label: ''
          }, {
            value: 0,
            label: ''
          }],
          options2: [
            {
              value: '',
              label: ''
            },
            {
            value: 'created_at',
            label: ''
          }, {
            value: 'logged_at',
            label: ''
          }],
          //
          loading:false,
          pageSize:15,//15
          //
          dialogVisible:false, //
          activeName:'info',
          id:1
        }
    },

    computed:{
      ...mapGetters({
        USERINFO_base:'USERINFO_base',
        versions:'versions'
      }),
      tableData(){
          return this.USERINFO_base.data.users
      }
    },
    watch:{
      USERINFO_base(data){
          return data
      }
    },
    methods:{
      ...mapActions({
        getInfo:'USERINFO_getUsers',
        ud_base:'UD_base_info',
        setId:'UD_setId'
      }),
      /**/
      filtration(){
        this.getuser(1);
      },
      search(){
        this.getInfo({q:this.ser,limit:this.pageSize}).then(res=>{
            if(res.data.data.users.length == 0){
              this.$message({
                message: '',
                type: 'warning',
                duration:1500
              });
            }
        })
      },
      /**/
      //
      getuser(number){

        var options = {
            page:number,
            app_version:this.filter.version==''?null:this.filter.version,
            app_channel:this.filter.channel==""?null:this.filter.channel,
            is_enabled:this.filter.str === ''?'':this.filter.str,
            date_type:this.filter.time?this.filter.time:'',
            start_at:this.filter.start?this.filtrationTime(this.filter.start):'',
            end_at:this.filter.end?this.filtrationTime(this.filter.end):'',
            limit:this.pageSize
          }


        this.getInfo(options).then(res=>{
          return  new Promise((resolve,reject)=>{
              resolve(res)
          })
        })
      },
      /**/
      detail(row){
        this.dialogVisible = true;
        window.sessionStorage.setItem('userId',row)
        this.ud_base()
      },
      cdialog(){
        this.dialogVisible = false
      },
      /**/
      handleCurrentChange(val) {
        this.getuser(val)
      }

    },
    mounted(){
      this.getInfo({limit:this.pageSize})
    }
  }
</script>

<style lang="less" scoped>
  #userInfo{
    padding: 10px;
  }
  /**/
  #userInfo .breadcrumb{
    height: 30px;
    line-height: 30px;
  }
  /**/
  .count{
    margin:10px 0;
    background-color:#fff;
    border:1px solid #D3DCE6;
    h2{
      font-size:14px;
      font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "", Arial, sans-serif;
      font-weight:400;
      text-align: left;
      span{
        margin-right: 10px;
        font-size:18px;
        color: #666666;
      }
    }
    ul{
      display: flex;
      margin:10px 0;
      li{
        width: 25%;
        height: 80px;
        margin-right: 10px;
        color: #fff;
        position:relative;
        div{
          top:20px;
          right: 10px;
          position: absolute;
          height: 30px;
          text-align: center;
          p{
            margin-bottom: 10px;
            font-size: 25px;
          }
        }
      }
      li:nth-child(1){
        background-color: #3598DC;
      }
      li:nth-child(2){
        background-color: #E7505A;
      }
      li:nth-child(3){
        background-color: #32C5D2;
      }
    }
  }
  .warp_list{
    width: 100%;
    overflow: auto;
    background-color: #fff;
    margin-top: 10px;
  }
  /**/
  .filter{
    text-align: left;
    padding: 10px;
    margin: 10px 0;
    background-color:#fff;
    border:1px solid #D3DCE6;
    span{
      font-size: 14px;
      color: #475669;
    }
  }
  .filter{
    .f{
      width: 250px;
      display: inline-block;
      line-height: 50px;
    }
  }
  .filter .el-select{
    width: 150px;
  }
  /**/
  .table{
    padding: 10px;
  }
  .table th,td{
    width: 150px;
  }
  /**/
  .page{
    text-align: right;
    margin-top: 20px;
  }
</style>

<style>
  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s
  }
  .fade-enter, .fade-leave-active {
    opacity: 0
  }
  .el-table .cell {
    word-break: break-all;
    line-height: 24px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
  }
  .el-dialog--small{
    width:60%;
  }
</style>



